.main-menu {
  @include List-Reset();

  display: flex;

  @media (max-width: 960px) {
    flex-direction: column;
  }

  & li {

    @media (max-width: 960px) {
      padding-right: 0;
      margin-bottom: 40px;
    }

    &:not(:last-child) {
      position: relative;
      padding-right: 40px;
      margin-right: 32px;

      @media (max-width: 1030px) {
        padding-right: 30px;
        margin-right: 23px;
      }

      @media (max-width: $medium) {
        padding-right: 24px;
        margin-right: 20px;
      }

      @media (max-width: 960px) {
        margin-right: 0;
      }

      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 10px;
        width: 8px;
        height: 8px;
        background-color: #C5D0CD;
        border-radius: 50%;

        @media (max-width: 960px) {
          display: none;
        }
      }
    }
  }

  & a {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    letter-spacing: 1.1px;
    text-decoration: none;
    text-transform: uppercase;
    color: var(--basic-dark);
    @include Transition();

    &:hover {
      color: var(--basic-green);
    }
  }
}

.main-menu__close {
  display: none;
  position: absolute;
  left: 10px;
  top: 10px;
  padding-left: 0;
  padding-right: 0;
  background-color: transparent;
  border: 0;
  z-index: 20;
  cursor: pointer;

  @media (max-width: 960px) {
    display: block;
  }
}

.main-menu__close-icon {
  position: relative;
  display: block;
  width: 21px;
  height: 21px;
  box-sizing: border-box;
  border: 1px solid var(--basic-dark);
  border-radius: 50%;
  transform: rotate(45deg);
  @include Transition();

  &:hover {
    border-color: var(--basic-green);

    &::before,
    &::after {
      background-color: var(--basic-green);
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: var(--basic-dark);
  }

  &::before {
    top: 9px;
    left: 3px;
    width: 13px;
    height: 1px;
  }

  &::after {
    top: 3px;
    left: 9px;
    width: 1px;
    height: 13px;
  }
}
