.card-tabs {
  margin-top: 94px;

  @media (max-width: 1023px) {
    margin-top: 47px;
  }

  @media (max-width: 480px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 18px;
  }

  & input {
    display: none;
  }
}

.card-tabs__item {
  position: relative;
  margin-right: 99px;
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 20px;
  cursor: pointer;
  @include Transition();

  @media (max-width: 1023px) {
    display: inline-flex;
    margin-right: 50px;
    margin-top: 27px;
  }

  @media (max-width: 480px) {
    align-items: center;
    min-height: 32px;
    padding-left: 10px;
    padding-right: 12px;
    margin-right: 19px;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0;
  }

  &:nth-of-type(2) {
    @media (max-width: 480px) {
      width: 111px;
      margin-right: 19px;
    }
  }

  &:nth-of-type(3) {
    @media (max-width: 480px) {
      width: 98px;
      margin-right: -10px;
    }
  }

  &:last-of-type {
    margin-right: 0;

    @media (max-width: 480px) {
      margin-right: -10px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -16px;
    width: 100%;
    height: 2px;
    background-color: var(--basic-green);
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  }

  &--youtube {
    @media (max-width: 480px) {
      display: none;
    }

    &::before {
      content: "";
      display: inline-block;
      vertical-align: text-top;
      width: 25px;
      height: 17px;
      margin-right: 7px;
      background-image: url("../images/ico-youtube.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  .card-tabs #tab1:checked + &,
  .card-tabs #tab2:checked + &,
  .card-tabs #tab3:checked + &,
  .card-tabs #tab4:checked + & {
    color: var(--basic-green);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -16px;
      width: 100%;
      height: 2px;
      background-color: var(--basic-green);
      transform: scaleX(1);
    }
  }

  &:hover {
    color: var(--basic-green);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -16px;
      width: 100%;
      height: 2px;
      background-color: var(--basic-green);
      transform: scaleX(1);
      transform-origin: left;
      transition: transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);
    }
  }
}

.card-tabs__content {
  margin-top: 9px;
  border-top: 2px solid #EBF1F0;

  @media (max-width: 480px) {
    margin-top: 14px;
  }

  & h2 {
    margin-top: 45px;
    margin-bottom: 24px;
    font-style: italic;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: var(--basic-green);

    @media (max-width: 480px) {
      margin-bottom: 0;
      margin-top: 23px;
      font-size: 18px;
      line-height: 18px;
    }

    & + p {
      @media (max-width: 480px) {
        margin-top: 10px;
      }
    }

    & ~ h2 {
      margin-top: 50px;

      @media (max-width: 480px) {
        margin-top: 33px;
      }
    }
  }

  & p {
    margin-bottom: 16px;
    margin-top: 16px;
    letter-spacing: 0.2px;
    font-size: 18px;
    line-height: 27px;

    @media (max-width: 480px) {
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0;
    }
  }
}
