.product-label {
  position: relative;

  &::before {
    position: absolute;
    top: 16px;
    left: 20px;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 13px;
    line-height: 21px;
    color: var(--basic-black);
  }

  &--bestseller {
    &::before {
      content: "";
    }
  }

  &--new {
    &::before {
      content: "";
    }
  }

  &--hit {
    &::before {
      content: "";
    }
  }
}
