.article-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(25% - 20px);
  margin-right: 10px;
  margin-left: 10px;
  padding-right: 18px;
  padding-left: 18px;
  box-sizing: border-box;
  border: 3px solid #F3F3F3;
  text-decoration: none;
  @include Transition();

  @media (max-width: 768px) {
    width: calc(50% - 20px);
    margin-bottom: 20px;
  }

  @media (max-width: 480px) {
    width: calc(50% - 10px);
    margin-right: 5px;
    margin-left: 5px;
    margin-bottom: 10px;
    padding-right: 9px;
    padding-left: 9px;
    padding-bottom: 22px;
  }

  &:hover {
    border-color: var(--basic-green);
  }
}

.article-item__thumbnail {
  margin-top: 38px;
  margin-bottom: 30px;

  @media (max-width: 480px) {
    width: 120px;
    height: 120px;
    margin-top: 27px;
    margin-bottom: 22px;
  }
}

.article-item__title {
  margin-bottom: 19px;
  font-weight: 500;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 0.1px;
  text-align: center;
  text-transform: uppercase;
  color: var(--basic-dark);

  @media (max-width: 960px) {
    font-size: 13px;
  }

  @media (max-width: 480px) {
    line-height: 17px;
  }
}

.article-item__more {
  margin-top: auto;
  margin-bottom: 28px;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
  color: var(--basic-green);
  text-decoration: none;

  &::after {
    margin-top: -2px;
    content: "";
    display: block;
    height: 4px;
    background-color: #EAF5C6;
  }

  @media (max-width: 480px) {
    display: none;
  }
}
