.reviews-slider {
  @media (max-width: 480px) {
    margin-right: 35px;
    margin-left: 35px;
  }

  @media (max-width: 320px) {
    margin-right: 0;
    margin-left: 0;
  }
}

.reviews-slider__item {
  display: flex !important;
  flex-wrap: wrap;
  align-items: center;
  border-radius: 9px;
  background-color: var(--basic-white);
  box-sizing: border-box;

  &--text {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 17px;
      top: -13px;
      width: 30px;
      height: 24px;
      background-image: url("../images/ico-quote.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  & p {
    margin-right: 17px;
    margin-left: 17px;
    font-size: 16px;
    line-height: 22px;
  }
}

.reviews-slider__text {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.reviews-slider__more-link {
  position: absolute;
  bottom: 17px;
  left: 17px;
  font-size: 16px;
  line-height: 22px;
  color: #6F6F6F;
  @include Transition();

  &:hover {
    color: var(--basic-green);
    text-decoration: underline;
  }
}

.reviews-slider__thumbnail {
  width: 100%;
  border-radius: 9px;
}
