.card-list {
  @include List-Reset(42px, 0, 0);

  font-size: 17px;
  line-height: 24px;
  color: #5A6870;

  @media (max-width: 480px) {
    margin-top: 25px;
    font-size: 16px;
    line-height: 22px;
  }

  & li:not(:last-child) {
    margin-bottom: 6px;
  }

  .card-tabs__content h2 ~ & {
    margin-top: 23px;

    @media (max-width: 480px) {
      margin-top: 15px;
    }
  }
}
