.section-title {
  margin-top: 20px;
  margin-bottom: 60px;
  font-size: 34px;
  font-weight: 400;
  line-height: 41px;
  text-align: center;

  @media (max-width: $medium) {
    font-size: 30px;
    line-height: 36px;
  }

  @media (max-width: 480px) {
    margin-bottom: 13px;

    @include Muller();

    font-size: 22px;
    line-height: 26px;
  }

  .about & {
    margin-top: 9px;

    @media (max-width: 480px) {
      margin-top: 0;
    }
  }

  .article & {
    margin-bottom: 57px;

    @media (max-width: 480px) {
      margin-top: -6px;
      margin-bottom: 35px;
    }
  }

  .scheme & {
    max-width: 900px;
    margin-top: 8px;

    @media (max-width: 480px) {
      margin-top: 0;
      margin-right: 20px;
      margin-left: 20px;
    }

    @media (max-width: 320px) {
      margin-right: 0;
      margin-left: 0;
    }
  }

  .reviews & {
    max-width: 500px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 43px;

    @media (max-width: 480px) {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  .complex & {
    margin-bottom: 45px;

    @media (max-width: 480px) {
      margin: -6px 20px 20px;
      line-height: 22px;
    }
  }
}

.section-title__sub {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  line-height: 13px;
  font-weight: 700;
  letter-spacing: 2.5px;
  text-align: center;
  text-transform: uppercase;
  color: var(--basic-green);

  .scheme & {
    @media (max-width: 480px) {
      margin-right: 20px;
      margin-left: 20px;
    }

    @media (max-width: 320px) {
      margin-right: 0;
      margin-left: 0;
    }
  }

  &::before,
  &::after {
    content: "";
    display: block;
    width: 18px;
    height: 1px;
    background-color: var(--basic-green);
  }

  &::before {
    margin-right: 8px;
  }

  &::after {
    margin-left: 8px;
  }
}
