.socials {
  @include List-Reset(7px, 0, 0);

  display: flex;
  justify-content: flex-end;

  @media (max-width: 1023px) {
    justify-content: center;
  }

  @media (max-width: 680px) {
    flex-direction: column;
    align-items: flex-end;
    margin-right: 60px;
    margin-top: 30px;
  }

  @media (max-width: 320px) {
    flex-direction: row;
    margin-right: 0;
  }
}

.socials__item {
  margin-left: 10px;

  @media (max-width: 1023px) and (min-width: 481px) {
    margin-right: 10px;
  }

  &:not(:last-child) {
    @media (max-width: 680px) {
      margin-bottom: 10px;
    }

    @media (max-width: 320px) {
      margin-bottom: 0;
      margin-right: 10px;
    }
  }

  & a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    background-color: #F4F4F4;
    border-radius: 50%;

    & svg {
      fill: var(--basic-dark);
      @include Transition();
    }

    &:hover {
      & svg {
        fill: var(--basic-green);
      }
    }
  }
}
