.page-footer {
  color: #5A6870;
}

.page-footer__inner {
  display: flex;
  align-items: flex-start;
  padding-top: 76px;
  padding-bottom: 70px;

  @media (max-width: 1023px) {
    flex-wrap: wrap;
  }

  @media (max-width: 480px) {
    padding-top: 45px;
    padding-bottom: 180px;
  }
}

.page-footer__item {
  &:nth-child(1) {
    margin-right: 18%;

    @media (max-width: $medium) {
      margin-right: 14%;
    }

    @media (max-width: 960px) {
      margin-right: 8%;
    }

    @media (max-width: 768px) {
      margin-right: auto;
    }

    @media (max-width: 680px) {
      width: 100%;
      margin-right: 0;
    }
  }

  &:nth-child(2) {
    @media (max-width: 1023px) {
      margin-left: auto;
    }

    @media (max-width: 680px) {
      margin-left: 0;
    }
  }

  &:nth-child(3) {
    margin-left: auto;

    @media (max-width: 1023px) {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    @media (max-width: 680px) {
      width: auto;
      flex-grow: 1;
    }
  }
}

.page-footer__detail {
  margin-bottom: 26px;

  & p {
    margin-bottom: 6px;
    margin-top: 2px;
    font-size: 15px;
    line-height: 18px;
  }

  & a {
    text-decoration: none;
    color: inherit;
  }
}

.page-footer__more {
  padding: 14px 36px 10px;
  margin-top: 18px;
  display: inline-block;
  margin-left: auto;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 13px;
  text-align: center;
  line-height: 21px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--basic-dark);
  border: 2px solid #F4F4F4;
  box-sizing: border-box;
  border-radius: 61px;
  @include Transition();

  .page-footer__inner > & {
    display: none;

    @media (max-width: 680px) {
      display: inline-block;
    }
  }

  .page-footer__item > & {
    @media (max-width: 680px) {
      display: none;
    }
  }

  @media (max-width: 1023px) {
    margin-right: auto;
  }

  @media (max-width: 480px) {
    margin-top: 15px;
  }

  @media (max-width: 320px) {
    margin-left: auto;
    margin-right: auto;
  }

  &:hover {
    color: var(--basic-green);
    border-color: var(--basic-green);
  }
}
