.delivery {
  width: calc(33.8% - 20px);
  padding: 33px 86px 35px 27px;
  box-sizing: border-box;
  border: 5px solid #DBE8E6;
  background-color: var(--basic-gray);

  @media (max-width: 1330px) {
    padding-right: 27px;
  }

  @media (max-width: 1023px) {
    width: 100%;
    margin-top: 30px;
  }

  @media (max-width: 768px) {
    max-width: 380px;
    margin-left: auto;
    margin-right: auto;
    padding-right: 46px;
  }
}

.delivery__detail {
  @include List-Reset();

  font-size: 15px;
  line-height: 15px;
  color: #6D7B79;

  @media (max-width: 1023px) and (min-width: 769px) {
    display: flex;
    justify-content: space-between;
  }
}

.delivery__item {
  display: flex;
  align-items: center;

  @media (max-width: 1023px) and (min-width: 769px) {
    width: calc((100% - 40px) / 3);
  }

  &:not(:last-child) {
    margin-bottom: 14px;
  }

  &::before {
    content: "";
    display: block;
    flex-shrink: 0;
    width: 46px;
    height: 46px;
    margin-right: 29px;
    background-color: var(--basic-white);
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
  }

  &--courier::before {
    background-image: url("../images/ico-delivery-1.svg");
  }

  &--mail::before {
    background-image: url("../images/ico-delivery-2.svg");
  }

  &--payment::before {
    background-image: url("../images/ico-delivery-3.svg");
  }
}

.delivery__more {
  display: inline-block;
  margin-top: 36px;
  margin-left: 75px;
  font-size: 15px;
  line-height: 15px;
  color: var(--basic-dark);
}
