.order {
  background-color: var(--basic-white);
}

.order__title {
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;

  @media (max-width: 480px) {
    margin-bottom: -5px;
    font-size: 20px;
    line-height: 24px;
  }
}

.order__complete {
  display: block;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 0 15px;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: var(--basic-green);
  border: 0;
  cursor: pointer;
  @include Transition();

  @media (max-width: 480px) {
    width: calc(100% - 20px);
  }

  &:hover {
    color: var(--basic-green);
    background-color: var(--basic-white);
  }
}
