.stages-detail {
  @include List-Reset(47px, 0, 0);

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: 90px;
  margin-right: 90px;

  @media (max-width: $medium) {
    margin-right: 0;
    margin-left: 0;
  }

  @media (max-width: 480px) {
    margin-top: 63px;
    margin-right: 20px;
    margin-left: 20px;
  }

  @media (max-width: 320px) {
    margin-left: 0;
    margin-right: 0;
  }
}

.stages-detail__item {
  width: calc(50% - 50px);

  @media (max-width: 768px) {
    width: calc(50% - 20px);
  }

  @media (max-width: 480px) {
    width: 100%;
  }

  &:not(:last-child) {
    @media (max-width: 480px) {
      margin-bottom: 4px;
    }
  }

  &:nth-child(1) {
    order: 1;
  }

  &:nth-child(2) {
    order: 3;

    @media (max-width: 480px) {
      order: 2;
    }
  }

  &:nth-child(3) {
    order: 2;
  }

  &:nth-child(4) {
    order: 4;
  }
}

.stages-detail__step {
  margin-bottom: 0;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 15px;
  line-height: 21px;
  text-transform: uppercase;
  color: var(--basic-green);

  @media (max-width: 480px) {
    margin-top: 0;
  }
}

.stages-detail__title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  letter-spacing: 0.7px;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}

.stages-detail__description {
  margin-top: 13px;
  letter-spacing: 0.3px;
  font-size: 17px;
  line-height: 24px;
  color: var(--basic-dark);

  @media (max-width: 480px) {
    margin-top: 3px;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0;
  }
}
