.card {
  padding-bottom: 95px;

  @media (max-width: 480px) {
    padding-bottom: 46px;
  }
}

.card__overlay {
  padding-top: 88px;
  padding-bottom: 103px;
  background-color: var(--basic-gray);
  background-image: url("../images/bg-card-overlay.png");
  background-repeat: no-repeat;
  background-position: left bottom;

  @media (max-width: 480px) {
    padding-top: 38px;
    padding-bottom: 40px;
    background-image: none;
  }
}

.card__inner {
  display: flex;
  align-items: flex-start;

  @media (max-width: 480px) {
    flex-wrap: wrap;
  }
}

.card__thumbnail {
  flex-shrink: 0;
  width: 40.77%;
  margin-right: 40px;

  @media (max-width: 1023px) {
    width: calc(50% - 10px);
    margin-right: 20px;
  }

  @media (max-width: 480px) {
    width: 100%;
    margin-right: 0;
  }
}

.card__content {
  width: calc(100% - 40.77% - 40px);
  flex-grow: 1;

  @media (max-width: 1023px) {
    width: calc(50% - 10px);
  }
}

.card__data {
  width: 100%;

  .card__inner > & {
    display: none;

    @media (max-width: 480px) {
      display: block;
    }
  }

  .card__content > & {
    @media (max-width: 480px) {
      display: none;
    }
  }
}

.card__title {
  margin-top: -2px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;

  @media (max-width: 480px) {
    margin-bottom: 14px;
    font-size: 20px;
    line-height: 24px;
  }
}

.card__sku {
  margin-top: 10px;
  font-size: 17px;
  line-height: 20px;
  color: #5A6870;

  @media (max-width: 480px) {
    margin-top: 15px;
  }
}

.card__price {
  margin-top: 12px;
  margin-bottom: 28px;
  font-weight: 500;
  font-size: 22px;
  line-height: 22px;
  text-transform: uppercase;

  @media (max-width: 480px) {
    margin-top: 25px;
    margin-bottom: 17px;
    font-size: 20px;
    line-height: 20px;
  }

  .card__sku + & {
    margin-top: 34px;
  }
}

.card__buttons {
  display: flex;
  padding-bottom: 40px;
  margin-top: 80px;
  margin-bottom: 42px;
  border-bottom: 1px dashed #889D9A;

  @media (max-width: 1023px) {
    flex-direction: column;
  }

  @media (max-width: 480px) {
    margin-top: 0;
    padding-bottom: 30px;
    margin-bottom: 36px;
  }

  .card__sku ~ & {
    margin-top: 0;
  }
}

.card__add-cart {
  display: flex;
  justify-content: center;
  width: 360px;
  padding-top: 24px;
  padding-bottom: 18px;
  letter-spacing: 1.3px;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--basic-dark);
  border: 2px solid var(--basic-green);
  background-color: var(--basic-gray);
  box-sizing: border-box;
  @include Transition();

  @media (max-width: 480px) {
    width: 100%;
  }

  &:hover {
    color: var(--basic-white);
    background-color: var(--basic-green);
    border-color: var(--basic-green);
  }

  &--custom {
    margin-left: 10px;
    border-color: #DBE7E5;
    background-color: #DBE7E5;

    @media (max-width: 1023px) {
      margin-left: 0;
      margin-top: 10px;
    }
  }
}

.card__detail {
  position: relative;
  padding-left: 50px;
  letter-spacing: 0.2px;
  font-size: 17px;
  line-height: 24px;
  color: #5A6870;

  @media (max-width: 480px) {
    padding-left: 45px;
    letter-spacing: 0;
    font-size: 16px;
    line-height: 19px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 34px;
    height: 34px;
    background-color: var(--basic-white);
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;

    @media (max-width: 480px) {
      top: -6px;
    }
  }

  & p {
    margin-bottom: 6px;

    @media (max-width: 480px) {
      font-size: 16px;
      line-height: 19px;
    }
  }

  &--about::before {
    background-image: url("../images/ico-check.svg");
  }

  &--delivery::before {
    top: 3px;
    background-image: url("../images/ico-car.svg");
  }

  &:not(:last-child) {
    margin-bottom: 32px;
  }

  & ul {
    @include List-Reset();
  }

  & li {
    &:not(:last-child) {
      @media (max-width: 480px) {
        margin-bottom: 10px;
      }
    }
  }

  & a {
    color: var(--basic-green);
    text-decoration: none;
  }
}

.card__detail-title {
  @media (max-width: 480px) {
    font-size: 17px;
    line-height: 24px;
  }
}
