.cart-item {
  @media (max-width: 680px) {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &:not(:last-child) {
    border-bottom: 1px dashed #9AADA5;
  }

  & td {
    padding: 20px 0 10px 20px;

    @media (max-width: 680px) {
      display: flex;
      box-sizing: border-box;
    }

    @media (max-width: 1200px) and (min-width: 1024px), (max-width: 768px) {
      padding-top: 10px;
      padding-left: 10px;
    }

    &:last-child {
      padding-right: 20px;

      @media (max-width: 1200px) and (min-width: 1024px), (max-width: 768px) {
        padding-right: 10px;
      }
    }
  }
}

.cart-item__thumbnail {
  @media (max-width: 768px) {
    width: 80px;
    height: auto;
  }
}

.cart-item__thumbnail-wrapper {
  @media (max-width: 680px) {
    display: flex;
    margin-right: 10px;
  }
}

.cart-item__title {
  font-size: 18px;
  line-height: 25px;
  text-decoration: none;
  color: #1B262D;

  @media (max-width: 768px) {
    font-size: 16px;
    line-height: 22px;
  }
}

.cart-item__title-wrapper {
  max-width: 374px;

  .cart-item & {
    @media (max-width: 680px) {
      width: calc(100% - 100px);
      max-width: 100%;
      padding-right: 10px;
    }
  }
}

.cart-item__quantity {
  .cart-item & {
    @media (max-width: 680px) {
      justify-content: center;
      width: calc(100% / 3);
    }

    @media (max-width: 480px) {
      justify-content: flex-start;
      width: 100%;
      margin-top: -10px;
      padding-right: 10px;
      padding-top: 0;
    }
  }
}

.cart-item__price-wrapper {
  .cart-item & {
    @media (max-width: 680px) {
      display: flex;
      justify-content: center;
      width: calc(100% / 3);
    }

    @media (max-width: 480px) {
      justify-content: flex-start;
      width: 100%;
      padding-right: 10px;
      padding-top: 0;
    }
  }
}

.cart-item__price {
  font-size: 18px;
  line-height: 25px;
  white-space: nowrap;

  @media (max-width: 480px) {
    margin-top: 14px;
    margin-bottom: 7px;
    margin-left: 100px;
  }
}

.cart-item__remove-wrapper {
  .cart-item & {
    @media (max-width: 680px) {
      display: flex;
      justify-content: center;
      width: calc(100% / 3);
    }

    @media (max-width: 480px) {
      justify-content: flex-start;
      width: 100%;
      padding-right: 10px;
      padding-top: 0;
      padding-bottom: 23px;
    }
  }
}

.cart-item__remove {
  position: relative;
  display: block;
  width: 22px;
  height: 22px;
  transform: rotate(45deg) scale(0.8);

  @media (max-width: 480px) {
    margin-left: 95px;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: #C0C0C0;
  }

  &::before {
    top: 10px;
    left: 0;
    width: 22px;
    height: 2px;
  }

  &::after {
    top: 0;
    left: 10px;
    width: 2px;
    height: 22px;
  }
}
