.page-header {
  position: relative;

  .home & {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    z-index: 10;
  }

  body:not(.home) & {
    background-color: var(--basic-white);
    box-shadow: 0 4px 37px #D2DFDD;
  }
}

.page-header__inner {
  display: flex;
  align-items: flex-start;
  padding-top: 28px;
  padding-bottom: 10px;
  border-bottom: 4px solid var(--basic-white);

  @media (max-width: 480px) {
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 18px;
    padding-bottom: 5px;
  }

  @media (max-width: 320px) {
    margin-left: 0;
    margin-right: 0;
  }

  body:not(.home) & {
    border-bottom: 2px solid transparent;
  }
}

.page-header__menu-wrapper {
  @media (max-width: 960px) {
    position: fixed;
    top: 0;
    right: -320px;
    bottom: 0;
    z-index: 20;
    width: 320px;
    padding: 50px;
    background-color: var(--basic-gray);
    box-sizing: border-box;
    @include Transition();
  }

  &.show {
    @media (max-width: 960px) {
      right: 0;
    }
  }
}
