.header-cart {
  position: relative;
  margin-left: auto;
  margin-top: 4px;
  margin-right: 12px;
  text-decoration: none;

  @media (max-width: 960px) {
    margin-top: -7px;
  }

  @media (max-width: 480px) {
    margin-top: -1px;
    height: 28px;
  }
}

.header-cart__ico {
  fill: var(--basic-dark);
  @include Transition();

  @media (max-width: 480px) {
    width: 27px;
  }

  .header-cart:hover & {
    fill: var(--basic-green);
  }
}

.header-cart__counter {
  position: absolute;
  right: -12px;
  top: -12px;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  padding-top: 2px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  color: var(--basic-dark);
  box-sizing: border-box;
  background-color: var(--basic-white);
  border-radius: 50%;

  @media (max-width: 480px) {
    top: -10px;
  }

  body:not(.home) & {
    background-color: var(--basic-gray);
  }
}
