.about-item {
  width: calc((100% - 40px) / 3);

  @media (max-width: 480px) {
    width: 100%;
    margin-bottom: 23px;
  }
}

.about-item__title {
  max-width: 300px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-transform: uppercase;
  color: var(--basic-green);

  @media (max-width: 480px) {
    margin-bottom: 10px;
  }
}

.about-item__description {
  @include List-Reset();

  font-size: 17px;
  line-height: 17px;

  @media (max-width: 480px) {
    font-size: 16px;
    line-height: 16px;
  }

  & li {
    position: relative;
    padding-left: 25px;

    @media (max-width: 480px) {
      padding-left: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 5px;
      width: 14px;
      height: 11px;
      background-image: url("../images/ico-green-check.svg");
      background-position: center;
      background-repeat: no-repeat;
    }

    &:not(:last-child) {
      margin-bottom: 17px;

      @media (max-width: 480px) {
        margin-bottom: 16px;
      }
    }
  }
}
