.footer-menu {
  @include List-Reset(-5px, 0, 0);

  @media (max-width: 680px) {
    margin-top: 32px;
  }

  & li {
    margin-bottom: 2px;

    @media (max-width: 480px) {
      margin-bottom: 12px;
    }
  }

  & a {
    font-size: 14px;
    line-height: 17px;
    color: #5A6870;
    text-decoration: none;
    @include Transition();

    @media (max-width: 480px) {
      font-size: 15px;
      line-height: 36px;
    }

    &:hover {
      color: var(--basic-green);
    }
  }
}
