.modal-product {
  display: flex;
  align-items: flex-start;
  font-size: 17px;
  line-height: 20px;

  @media (max-width: 768px) {
    flex-direction: column;
  }
}

.modal-product__thumbnail {
  margin-right: 31px;
  flex-shrink: 0;

  @media (max-width: 768px) {
    margin-bottom: 13px;
  }
}

.modal-product__description {
  margin-top: 3px;
  color: #8C8C8C;
}

.modal-product__title {
  margin-top: 20px;
  margin-bottom: 0;
  color: var(--basic-black);
}

.modal-product__price {
  margin-top: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: var(--basic-green);
}
