.promo {
  position: relative;
  height: 1000px;
  background-color: var(--basic-light-gray);
  background-image: url("../images/bg-promo-decoration.png");
  background-position: 30px 100%;
  background-repeat: no-repeat;
  overflow: hidden;

  @media (max-width: $template-desktop) {
    height: 740px;
  }

  @media (max-width: $medium) {
    height: 660px;
  }

  @media (max-width: 480px) {
    height: 480px;
    background-image: url("../images/bg-promo-mob.jpg");
    background-position: center bottom;
    background-repeat: no-repeat;
  }

  &::before {
    content: "";
    position: absolute;
    width: 67px;
    height: 1008px;
    background-image: url("../images/bg-promo-figure.svg");
    background-position: center;
    background-repeat: no-repeat;

    @media (max-width: $template-desktop) {
      left: -27px;
      height: 740px;
      width: 77px;
      background-position: center right;
      background-size: contain;
    }

    @media (max-width: $medium) {
      left: -32px;
      height: 670px;
    }

    @media (max-width: 480px) {
      height: 480px;
      left: -45px;
    }
  }
}

.promo__girl {
  position: absolute;
  bottom: 0;
  right: -1px;

  @media (max-width: $template-desktop) {
    bottom: -30px;
    width: 612px;
  }

  @media (max-width: $large) {
    width: 500px;
  }

  @media (max-width: $medium) {
    bottom: 0;
    width: 450px;
  }

  @media (max-width: 768px) {
    width: 300px;
  }

  @media (max-width: 480px) {
    display: none;
  }
}

.promo__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.promo__decoration {
  position: absolute;
  left: 0;
  top: 220px;

  @media (max-width: $template-desktop) {
    left: 4vw;
    top: 148px;
    height: 530px;
  }

  @media (max-width: $extra-large) {
    left: 50px;
  }

  @media (max-width: $large) {
    top: 180px;
    height: 450px;
  }

  @media (max-width: $medium) {
    top: 150px;
    height: 400px;
  }

  @media (max-width: 960px) {
    left: 10px;
    top: 170px;
    height: 350px;
  }

  @media (max-width: 768px) {
    left: 50%;
    top: -34px;
    width: auto;
    margin-left: -52px;
    transform: rotate(90deg);
  }

  @media (max-width: 480px) {
    position: inherit;
    width: auto;
    height: auto;
    margin-top: 80px;
    margin-left: 0;
    transform: rotate(0);
  }
}

.promo__text {
  margin-top: 220px;

  @media (max-width: $template-desktop) {
    margin-top: 150px;
  }

  @media (max-width: $medium) {
    width: 400px;
  }

  @media (max-width: 768px) {
    margin-top: 240px;
  }

  @media (max-width: 480px) {
    width: 248px;
    margin-top: 20px;
  }
}

.promo__description {
  max-width: 620px;
  margin-bottom: 44px;
  margin-top: 33px;
  font-size: 44px;
  line-height: 44px;
  font-weight: 100;
  text-align: center;

  @media (max-width: $medium) {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 36px;
    line-height: 38px;
  }

  @media (max-width: 768px) {
    max-width: 500px;
    font-size: 30px;
  }

  @media (max-width: 480px) {
    max-width: 300px;
    margin-top: 8px;
    font-size: 20px;
    line-height: 20px;
    font-weight: 300;
  }
}

.promo__thumbnail {
  filter: drop-shadow(-41px 35px 49px rgba(50, 85, 77, 0.21));

  @media (max-width: $medium) {
    width: 400px;
  }

  @media (max-width: 768px) {
    width: 300px;
  }

  @media (max-width: 480px) {
    display: none;
  }
}
