.footnote {
  padding-top: 23px;
  padding-bottom: 17px;
  border-bottom: 1px solid #EEEEEE;

  .home & {
    @media (max-width: 480px) {
      display: none;
    }
  }

  .complex-card & {
    @media (max-width: 480px) {
      display: none;
    }
  }

  .card + & {
    padding-top: 20px;
    border-top: 1px solid #EEEEEE;
    border-bottom: none;

    @media (max-width: 480px) {
      padding-top: 11px;
      padding-bottom: 8px;
    }
  }

  & p {
    max-width: 976px;
    font-size: 14px;
    line-height: 22px;

    @media (max-width: 480px) {
      margin-left: 10px;
      line-height: 17px;
    }
  }
}
