.components-overlay {
  padding-top: 80px;
  padding-bottom: 43px;
  background-color: var(--basic-gray);

  @media (max-width: 768px) {
    padding-top: 45px;
  }

  @media (max-width: 480px) {
    padding-top: 15px;
    padding-bottom: 1px;
  }

  &:last-child {
    padding-top: 57px;

    @media (max-width: 480px) {
      padding-top: 30px;
    }
  }
}
