.form-field {
  display: block;
  width: 100%;
  padding: 15px 20px 13px;
  font-size: 16px;
  line-height: 16px;
  color: var(--basic-black);
  border: 2px solid #EDEDED;
  box-sizing: border-box;
  @include Transition();

  &:focus {
    border-color: var(--basic-green);
  }

  &::placeholder {
    font-size: 16px;
    line-height: 16px;
    font-family: inherit;
    color: #6F6F6F;
    opacity: 1;
  }

  &--radio {
    display: none;
  }

  &--textarea {
    max-width: 1060px;
    height: 150px;
    resize: none;

    @media (max-width: 480px) {
      height: 200px;
    }
  }
}

.form-field__wrapper {
  display: block;
  width: 280px;
  margin-right: 20px;

  .order-item--personal-data & {
    @media (max-width: 1330px) {
      width: calc(25% - 20px);
      max-width: 280px;
    }

    @media (max-width: 768px) {
      width: calc(50% - 10px);
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 9px;
    }

    @media (max-width: 480px) {
      width: 100%;
    }
  }

  &:last-child {
    margin-right: 0;
  }

  .order-item--delivery-method & {
    position: relative;
    width: auto;
    margin-right: 54px;

    @media (max-width: 1030px) {
      margin-right: 20px;
    }

    @media (max-width: 960px) {
      margin-right: 0;
      margin-bottom: 30px;
    }

    @media (max-width: 480px) {
      margin-bottom: 20px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .order-item--location & {
    @media (max-width: 1330px) {
      width: calc(25% - 20px);
      max-width: 280px;
    }

    @media (max-width: 768px) {
      width: calc(50% - 10px);
      margin-right: 0;
      margin-bottom: 10px;
      max-width: 100%;
    }

    @media (max-width: 480px) {
      width: 100%;
    }
  }

  .order-item--payment-method & {
    width: auto;
    margin-right: 50px;

    @media (max-width: 1030px) {
      margin-right: 40px;
    }

    @media (max-width: 480px) {
      width: 100%;
      box-sizing: border-box;
      margin-right: 0;
    }

    &:not(:last-child) {
      @media (max-width: 480px) {
        margin-bottom: 18px;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .order-item--comment & {
    width: 100%;
    margin-right: 0;
  }

  .place-detail & {
    width: 140px;

    @media (max-width: 1330px) {
      width: calc((100% - 40px) / 3);
      max-width: 140px;
    }

    @media (max-width: 768px) {
      width: calc((100% - 20px) / 3);
      max-width: 100%;
    }
  }
}

.form-field__title {
  position: relative;
  display: inline-block;
  letter-spacing: 0.4px;
  padding-left: 26px;

  @media (max-width: 480px) {
    letter-spacing: 0;
  }

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: calc(50% - 10px);
    display: block;
    width: 16px;
    height: 16px;
    margin-right: 10px;
    border-radius: 50%;
    background-color: var(--basic-white);
    border: 1px solid #BDBDBD;
    box-sizing: border-box;
  }

  & a {
    margin-left: 6px;
    color: var(--basic-green);

    @media (max-width: 480px) {
      display: inline-block;
      margin-left: 0;
      margin-top: 6px;
    }
  }

  .order-item--delivery-method & {
    font-size: 16px;
    line-height: 16px;
    color: #6F6F6F;
  }

  .form-field--radio:checked ~ & {
    color: var(--basic-black);

    &::before {
      border-color: var(--basic-green);
      border-width: 4px;
    }
  }
}
