.faq {
  margin-top: 100px;
  padding-bottom: 62px;

  @media (max-width: 480px) {
    margin-top: 40px;
    padding-bottom: 20px;
  }
}

.faq__person {
  display: block;
  margin-right: auto;
  margin-left: auto;
  border-radius: 50%;
  border: 5px solid var(--basic-green);
}

.faq__title {
  margin-top: 27px;
  margin-bottom: 0;
  font-size: 34px;
  line-height: 41px;
  text-align: center;

  @media (max-width: $medium) {
    font-size: 30px;
    line-height: 36px;
  }

  @media (max-width: 480px) {
    font-size: 22px;
    line-height: 26px;
  }
}

.faq__sub-title {
  margin-top: 12px;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  text-align: center;
  color: var(--basic-green);

  @media (max-width: 480px) {
    margin-top: 5px;
  }
}

.faq__list {
  margin: 74px 110px 0;

  @media (max-width: $medium) {
    margin-right: 50px;
    margin-left: 50px;
  }

  @media (max-width: 480px) {
    margin-top: 25px;
    margin-left: 0;
    margin-right: 0;
  }
}
