.menu-trigger {
  display: none;
  padding-right: 0;
  padding-left: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  letter-spacing: 1.1px;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--basic-dark);
  border: none;
  background-color: transparent;

  @media (max-width: 960px) {
    display: flex;
    align-items: center;
  }
}

.menu-trigger__icon {
  position: relative;
  display: block;
  width: 22px;
  height: 11px;
  margin-right: 6px;
  margin-top: -1px;
}

.menu-trigger__line {
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--basic-green);

  &:nth-child(2) {
    top: calc(50% - 1px);
  }

  &:last-child {
    width: 10px;
    bottom: 0;
  }
}
