.slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 0;
  padding-left: 0;
  border: 0;
  cursor: pointer;
  @include Transition();

  &:hover {
    border-color: var(--basic-green);

    & svg {
      fill: var(--basic-green);
    }
  }

  .reviews & {
    width: 40px;
    height: 40px;
    margin-top: -20px;
    border-radius: 50%;
    background-color: transparent;
    border: 1px solid #CECECE;

    @media (max-width: $extra-large) {
      background-color: #f4f4f4;
    }

    @media (max-width: 480px), (max-width: 1366px) and (min-width: 1331px) {
      border-color: transparent;
      background-color: transparent;
      width: 17px;
      height: 28px;
      margin-top: -14px;
    }

    &:hover {
      border-color: var(--basic-green);

      @media (max-width: 480px), (max-width: 1366px) and (min-width: 1331px) {
        border-color: transparent;
      }

      & svg {
        fill: var(--basic-green);
      }
    }
  }

  .reviews &.slick-prev {
    left: -65px;

    @media (max-width: $template-desktop) {
      left: -60px;
    }

    @media (max-width: 1366px) and (min-width: 1331px) {
      left: -25px;
    }

    @media (max-width: $extra-large) {
      left: 15px;
    }

    @media (max-width: 480px) {
      left: -33px;
    }

    @media (max-width: 320px) {
      left: 15px;
    }
  }

  .reviews &.slick-next {
    right: -65px;

    @media (max-width: $template-desktop) {
      right: -60px;
    }

    @media (max-width: 1366px) and (min-width: 1331px) {
      right: -25px;
    }

    @media (max-width: $extra-large) {
      right: 15px;
    }

    @media (max-width: 480px) {
      right: -33px;
    }

    @media (max-width: 320px) {
      right: 15px;
    }
  }

  & svg {
    fill: #4B4B4B;
    @include Transition();

    @media (max-width: 480px), (max-width: 1366px) and (min-width: 1331px) {
      width: 17px;
      height: 28px;
    }
  }
}
