.product {
  display: flex;
  flex-direction: column;
  width: calc(25% - 20px);
  margin: 10px;
  padding: 20px;
  background-color: var(--basic-white);
  box-sizing: border-box;
  @include Transition();

  @media (max-width: 768px) {
    width: calc((100% / 3) - 20px);
  }

  @media (max-width: 480px) {
    width: calc(50% - 10px);
    margin: 5px;
    padding: 7px 6px 30px;
  }

  &:hover {
    box-shadow: 0 4px 24px #CFDCD8;
  }
}

.product__thumbnail-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 243px;
  margin-top: 17px;
  margin-bottom: 8px;

  @media (max-width: $medium) {
    height: 200px;
  }

  @media (max-width: 480px) {
    height: 143px;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.product__stage {
  letter-spacing: 1px;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #A5C5BC;

  @media (max-width: 480px) {
    margin-top: 6px;
  }
}

.product__title {
  margin-top: 12px;
  margin-bottom: 10px;
  font-size: 17px;
  text-decoration: none;
  line-height: 20px;
  text-align: center;
  color: var(--basic-black);

  @media (max-width: $medium) {
    font-size: 15px;
  }

  @media (max-width: 480px) {
    min-height: 90px;
    margin-top: 0;
    margin-bottom: 2px;
    line-height: 18px;
  }

  &:hover {
    color: var(--basic-green);
  }
}

.product__active {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;

  @media (max-width: 480px) {
    flex-direction: column;
  }
}

.product__price {
  margin-top: 10px;
  margin-bottom: 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--basic-green);

  @media (max-width: 480px) {
    margin-bottom: 20px;
    font-weight: 400;
  }
}

.product__add-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: var(--basic-light-gray);
  border-radius: 50%;
  @include Transition();

  & svg {
    width: 20px;
    height: 18px;
    fill: var(--basic-dark);
    @include Transition();
  }

  &:hover {
    background-color: var(--basic-green);

    & svg {
      fill: var(--basic-white);
    }
  }
}
