.history-brand {
  position: relative;
  margin-top: 88px;
  padding-top: 49px;
  padding-left: 80px;
  padding-bottom: 69px;

  @media (max-width: $template-desktop) {
    margin-left: 220px;
    padding-left: 100px;
  }

  @media (max-width: $medium) {
    margin-left: 0;
  }

  @media (max-width: 768px) {
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 15px;
    padding-right: 15px;
  }

  @media (max-width: 480px) {
    margin-left: 0;
    margin-right: 0;
    margin-top: 45px;
    padding: 30px 0 35px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: calc(100vw - ((100vw - 1420px) / 2));
    background-color: #EFF5F4;
    background-image: url("../images/bg-about-brand.png");
    background-position: right bottom;
    background-repeat: no-repeat;

    @media (max-width: $template-desktop) {
      background-position: -180px 140px;
    }
  }

  & p {
    max-width: 770px;
    margin-bottom: 14px;
    margin-top: 14px;
    font-size: 17px;
    line-height: 27px;

    @media (max-width: 480px) {
      font-size: 16px;
      line-height: 22px;
    }
  }
}

.history-brand__inner {
  position: relative;
  max-width: 810px;
}

.history-brand__title {
  margin-bottom: 41px;
  font-size: 34px;
  line-height: 41px;
  font-weight: 400;

  @media (max-width: $medium) {
    font-size: 30px;
    line-height: 36px;
  }

  @media (max-width: 480px) {
    margin-bottom: 13px;
    font-size: 22px;
    line-height: 26px;
  }
}


