.about {
  padding-top: 86px;
  padding-bottom: 100px;

  @media (max-width: $medium) {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  @media (max-width: 480px) {
    padding-top: 37px;
    padding-bottom: 70px;
  }
}

.about__list {
  display: flex;
  justify-content: space-between;

  @media (max-width: 480px) {
    flex-wrap: wrap;
    margin-left: 20px;
    margin-right: 20px;
  }

  @media (max-width: 320px) {
    margin-right: 0;
    margin-left: 0;
  }
}

.about__more {
  padding: 19px 41px 15px;
  margin-top: 48px;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  text-decoration: none;
  text-transform: uppercase;
  color: #1B262D;
  border: 2px solid #DFDFDF;
  @include Transition();

  @media (max-width: 480px) {
    margin-top: 15px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media (max-width: 320px) {
    padding-left: 5px;
    padding-right: 5px;
    letter-spacing: 0;
  }

  &:hover {
    color: var(--basic-green);
    border-color: var(--basic-green);
  }
}
