.page-title {
  font-weight: 300;
  font-size: 30px;
  line-height: 42px;

  @media (max-width: 480px) {
    font-size: 20px;
    line-height: 24px;
  }

  .cart__overlay & {
    margin-bottom: 27px;

    @media (max-width: 480px) {
      margin-top: 21px;
    }
  }

  &--custom {
    margin-top: 24px;
    margin-bottom: 54px;
    font-weight: 400;
    font-size: 34px;
    line-height: 41px;
    letter-spacing: 1.5px;

    @media (max-width: $medium) {
      font-size: 30px;
      line-height: 36px;
    }

    @media (max-width: 480px) {
      margin-bottom: 20px;
      font-size: 22px;
      line-height: 26px;
    }
  }
}
