.order-item {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-bottom: 59px;

  @media (max-width: 768px) {
    padding-bottom: 25px;
  }

  @media (max-width: 480px) {
    padding-bottom: 21px;
  }

  &:not(:last-of-type) {
    margin-bottom: 35px;
    border-bottom: 1px dashed #A7A7A7;

    @media (max-width: 768px) {
      margin-bottom: 5px;
    }
  }

  &--delivery-method {
    padding-bottom: 214px;

    @media (max-width: 960px) {
      flex-direction: column;
      padding-bottom: 29px;
    }

    @media (max-width: 480px) {
      padding-bottom: 0;
    }
  }

  &--location {
    @media (max-width: 768px) {
      justify-content: space-between;
    }
  }

  &--personal-data {
    @media (max-width: 768px) {
      justify-content: space-between;
    }
  }

  &--comment {
    @media (max-width: 480px) {
      padding-bottom: 50px;
    }
  }
}

.order-item__title {
  width: 100%;
  margin-top: 23px;
  margin-bottom: 31px;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--basic-green);

  @media (max-width: 480px) {
    margin-top: 25px;
    margin-bottom: 17px;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0;
  }
}
