.container {
  max-width: 1300px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;

  @media (max-width: 480px) {
    padding-right: 10px;
    padding-left: 10px;
  }

  .about & {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .components-overlay + & {
    padding-top: 58px;
    padding-bottom: 40px;

    @media (max-width: 480px) {
      padding-top: 32px;
      padding-bottom: 1px;
    }
  }
}
