.stages-item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: calc((100% - 130px * 3) / 4);
  padding-bottom: 40px;
  padding-right: 41px;
  padding-left: 29px;
  background-image: url("../images/bg-stage-item-only.png");
  background-repeat: no-repeat;
  background-position: 50% 100%;
  background-size: contain;
  box-sizing: border-box;

  &:nth-child(2) {
    padding-right: 35px;

    @media (max-width: 480px) {
      margin-bottom: 85px;
    }
  }

  &:nth-child(3) {
    @media (max-width: 480px) {
      margin-bottom: 105px;
    }
  }

  @media (max-width: $medium) {
    width: calc((100% - 80px * 3) / 4);
  }

  @media (max-width: 768px) {
    width: 50%;
    max-width: 217px;
    margin-right: calc((50% - 217px) / 2);
    margin-left: calc((50% - 217px) / 2);
    margin-bottom: 40px;
  }

  @media (max-width: 480px) {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 76px;
  }

  &:last-child {
    @media (max-width: 480px) {
      margin-bottom: 0;
    }
  }

  &:nth-child(2n)::after {
    @media (max-width: 768px) {
      display: none;
    }

    @media (max-width: 480px) {
      display: block;
    }
  }


  &:not(:last-child)::after {
    content: "";
    position: absolute;
    right: -75px;
    top: 125px;
    width: 20px;
    height: 33px;
    background-image: url("../images/ico-right-arrow.svg");
    background-repeat: no-repeat;
    background-position: 0 0;

    @media (max-width: $medium) {
      right: -40px
    }

    @media (max-width: 768px) {
      right: -90px
    }

    @media (max-width: 480px) {
      right: 105px;
      transform: rotate(90deg);
      top: initial;
      bottom: -60px;
    }
  }
}

.stages-item__step {
  position: absolute;
  top: 35px;
  right: -15px;
  padding: 11px 15px;
  letter-spacing: 1px;
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  text-transform: uppercase;
  color: var(--basic-green);
  background-color: var(--basic-white);
  border-radius: 20px;

  &::before {
    content: "";
    position: absolute;
    left: -8px;
    top: 13px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 7.5px 10px 7.5px 0;
    border-color: transparent #ffffff transparent transparent;
  }

  .stages-item:nth-child(1) &,
  .stages-item:nth-child(2) & {
    right: -40px;
  }

  .stages-item:nth-child(2) & {
    @media (max-width: 480px) {
      top: 23px;
    }
  }

  .stages-item:nth-child(3) & {
    right: -20px;

    @media (max-width: 480px) {
      top: 19px;
    }
  }

  .stages-item:last-child & {
    @media (max-width: 480px) {
      top: -23px;
    }
  }
}

.stages-item__thumbnail {
  filter: drop-shadow(-41px 35px 49px rgba(50, 85, 77, 0.18));

  .stages-item:nth-child(1) & {
    transform: translateX(-16px);
  }

  .stages-item:nth-child(2) & {
    transform: translateX(-18px);
  }

  .stages-item:nth-child(3) & {
    transform: translateX(-12px);
  }

  .stages-item:nth-child(4) & {
    transform: translate(-6px, 13px);
  }
}
